import { Box, Container, ExpandableSection, Header, Link, SpaceBetween } from "@amzn/awsui-components-react";
import { ContentProps } from "./Content";
import { Finding, Location, TaskSummary, TaskSummaryMap, getImportance } from "../types/acatData";
import { getAssetPath } from "../utils/assetUtils";

interface AutoRemediationProps {
  json: string;
}

interface CodeLinkProps {
  location: Location;
  taskSummary: TaskSummary;
}

const getCodeBlockClass = (line: string): string => {
  if (line.startsWith('@@')) {
    return 'code-block-info';
  }
  if (line.startsWith('-')) {
    return 'code-block-remove';
  }
  if (line.startsWith('+')) {
    return 'code-block-add';
  }
  return 'code-block-standard';
}

function AutoRemediation({ json }: AutoRemediationProps) {
  const data: any[] = JSON.parse(json);
  return (
    <>
      {data.map((item, i) =>
        <SpaceBetween key={i} size="m">
          <Box variant="h3">Auto Remediation:</Box>
          <Box>{item.description}</Box>
          <Box variant="code">{item.path}</Box>
          <Container>
            <Box variant="code" >
              {item.diff.split('\n').map((line: string, i: number) => 
                <div key={i} className={getCodeBlockClass(line)}>{line}</div>
              )}
            </Box>
          </Container>
        </SpaceBetween>
      )}
    </>
  );
}

function CodeLink({location, taskSummary}: CodeLinkProps) {
  const root = getAssetPath(location, taskSummary);
  const lineNumber = location.startLine === location.endLine
    ? location.startLine
    : `${location.startLine}-${location.endLine}`;
  const path = `${root}${location.path}#${lineNumber}`;
  return (
    <Link href={path}>{path}</Link>
  );
}

export function Findings({ acatOuput }: ContentProps) {
  const taskSummaryMap: TaskSummaryMap = {};
  acatOuput.jobRecord.taskSummaries.forEach(summary => {
    taskSummaryMap[summary.taskId] = summary;
  });

  const items: Finding[] = [...acatOuput.jobRecord.findings];
  items.sort((a: Finding, b: Finding) => getImportance(b.importance) - getImportance(a.importance));

  return (
    <>
      <div id="Findings"/>
      <Container header={<Header>Findings</Header>}>
        <SpaceBetween size="m">
          {items.length > 0
            ? items.map((finding: Finding) => 
              <ExpandableSection
                key={finding.findingId}
                headerText={<Box variant="h3">[<span className={`importance-${finding.importance}`}>{finding.importance}</span>] {finding.vulnerability}</Box>}
              >
                <SpaceBetween size="m">
                  <Box>Finding Id: <Box display="inline" variant="code">{finding.findingId}</Box></Box>
                  <Box>Rule Id: <Box display="inline" variant="code">{finding.ruleId}</Box></Box>
                  <Box>{finding.remediation}</Box>
                  {finding.assetLocations.map((location: Location, i: number) => <CodeLink key={i} location={location} taskSummary={taskSummaryMap[finding.taskId]}/>)}
                  {finding.findingMetadata.autoRemediation && 
                    <AutoRemediation json={finding.findingMetadata.autoRemediation} />
                  }
                </SpaceBetween>
              </ExpandableSection>
            )
          : <Box>0 findings produced</Box>
        }
        </SpaceBetween>
      </Container>
    </>
  );
}
