import { Alert, Header, SpaceBetween } from "@amzn/awsui-components-react";
import { AcatOutput } from "../types/acatData";
import { Summary } from "./Summary";
import { Findings } from "./Findings";
import { Status } from "./Status";

export interface ContentProps {
  acatOuput: AcatOutput;
}

/**
 * Main Page Body.
 */
export function Content({ acatOuput }: ContentProps) {
  return (
    <SpaceBetween size="m">
      <Header variant="h1">Acat Report</Header>
      {acatOuput.jobRecord.jobSummary.status == 'COMPLETE'
        ? <Alert type="success">Job completed successfully.</Alert>
        : <Alert type="error">Job failed with message: {acatOuput.jobRecord.jobSummary.statusMessage}</Alert>
      }
      <Summary acatOuput={acatOuput} />
      <Status taskSummaries={acatOuput.jobRecord.taskSummaries} />
      <Findings acatOuput={acatOuput} />
    </SpaceBetween>
  );
}
