import { useState } from 'react';
import './App.css';
import { AppLayout, Container } from '@amzn/awsui-components-react';
import { Navigation } from './componenets/Navigation';
import { Content } from './componenets/Content';
import { Help } from './componenets/Help';
import { AcatOutput } from './types/acatData';

function App() {
  const [data, setData] = useState<AcatOutput>();

  if (!data) {
    ((input: any) => {
      // Load Data from AcatOutput file.
      const json = input['ACAT_OUTPUT'];
      if (!json) {
        return;
      }
      setData(json);
    })(window);
  }

  return (
    <AppLayout 
      contentType='default'
      content={data ? <Content acatOuput={data} /> : <Container>Please Ensure this is run in the same directory as "acat-output.json"...</Container>}
      tools={<Help />}
      navigation={<Navigation/>}
    />
  );
}

export default App;
