import { SideNavigation, SideNavigationProps } from "@amzn/awsui-components-react";

interface NavigationProps {
  activeHref?: string;
}

/**
 * Navigation popout, pointing to anchors on the page.
 */
export function Navigation({activeHref}: NavigationProps) {
  const items: SideNavigationProps.Item[] = [
    {
      type: 'link',
      text: 'Summary',
      href: '#Summary',
    },
    {
      type: 'link',
      text: 'Status',
      href: '#Status',
    },
    {
      type: 'link',
      text: 'Findings',
      href: '#Findings',
    }
  ];
  return (
    <SideNavigation items={items} header={{ text: 'Contents', href: '' }} activeHref={activeHref} />
  );
}
