import { Container, Header, Table, TableProps } from "@amzn/awsui-components-react";
import { TaskSummary } from "../types/acatData";

export interface StatusProps {
  taskSummaries: TaskSummary[];
}

export function Status({ taskSummaries }: StatusProps) {
  const columnDefinitions: TableProps.ColumnDefinition<TaskSummary>[] = [
    {
      id: 'shimId',
      header: 'Analyzer Id',
      cell: (item: TaskSummary) => item.shimConfig.id,
      isRowHeader: true,
    },
    {
      id: 'status',
      header: 'Status',
      cell: (item: TaskSummary) => <div className={`status-${item.status}`}>{item.status}</div>,
      isRowHeader: true,
    },
    {
      id: 'statusMessage',
      header: 'Status Message',
      cell: (item: TaskSummary) => item.statusMessage,
      isRowHeader: true,
    },
    {
      id: 'duration',
      header: 'Duration',
      cell: (item: TaskSummary) => item.durationInMs,
      isRowHeader: true,
    },
  ];

  return (
    <>
      <div id="Status"/>
      <Container header={<Header>Status</Header>}>
        <Table 
          columnDefinitions={columnDefinitions}
          items={taskSummaries}
          sortingDisabled={true}
        />
      </Container>
    </>
  );
}