export interface TaskSummaryMap {
  [key: string]: TaskSummary;
}

export interface AssetContainer {
  root: string;
  type: string;
  gitfarmPackages: any[];
}

export interface ShimConfig {
  id: string;
  name: String;
}

export interface TaskSummary {
  taskId: string;
  jobId: string;
  shimConfig: ShimConfig;
  assetContainer: AssetContainer;
  status: string;
  statusMessage: string;
  durationInMs: number;
}

export interface Location {
  path: string;
  startLine: string;
  endLine: string;
  message: string;
  asset: any;
}

export interface Finding {
  findingId: string;
  jobId: string;
  taskId: string;
  ruleId: string;
  confidence: string;
  vulnerability: string;
  remediation: string;
  importance: string;
  assetLocations: Location[];
  findingMetadata: any;
}

export interface JobSummary {
  status: string;
  statusMessage: string;
  durationInMs: number;
}

export interface JobRecord {
  findings: any[];
  jobSummary: JobSummary;
  taskSummaries: TaskSummary[];
}

export interface AcatOutput {
  jobRecord: JobRecord;
  filteredRulesSummary: string;
  filteredFindingsSummary: string;
}

export const getImportance = (value: string): number => {
  switch(value) {
    case 'CRITICAL':
      return 4;
    case 'HIGH':
      return 3;
    case 'MEDIUM':
      return 2;
    case 'LOW':
      return 1;
    default:
      return 0;
  }
}
