import { Location, TaskSummary } from "../types/acatData";

export const getAssetPath = (location: Location, taskSummary: TaskSummary): string => {
  if (taskSummary.assetContainer.type == 'BrazilWorkspace') {
    const assetId = location.asset['@ref'];
    const assetItems = taskSummary.assetContainer.gitfarmPackages.filter(p => p['@id'] == assetId);
    if (!assetItems.length) {
      return taskSummary.assetContainer.root;
    }
    return `${taskSummary.assetContainer.root}src/${assetItems[0].name}/`;
  }
  return taskSummary.assetContainer.root;
}
